import React from 'react';
import ReactDOM from 'react-dom';
import { ThemeProvider, createTheme, Arwes, Frame, Header, Animation } from 'arwes';

function App() {
    return (
      <ThemeProvider theme={createTheme()}>
        <Arwes animate show>
          {anim => (
            <div style={{ padding: 20 }}>
              <Animation animate timeout={1000}>
              <Header animate style={{ 'margin-bottom': 40 }}>
                <h1>Geoffrey's Blog</h1>
              </Header>
              <Frame animate level={1} corners={4}>
                {anim => (
                  <div style={{ padding: '20px 40px' }}>
                    <h2>Some Topic Here</h2>
                    <p>"Lorem sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat. Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur. Excepteur sint occaecat cupidatat non proident, sunt in culpa qui officia deserunt mollit anim id est laborum."</p>
                  </div>
                )}
              </Frame>
              </Animation>
            </div>
          )}
        </Arwes>
      </ThemeProvider>
    );
}

ReactDOM.render(
  <React.StrictMode>
    <App />
  </React.StrictMode>,
  document.getElementById('root')
);
